import store from '@/store'
// uni.request
// 
let config = require("../config.js")
const request = (options) => {
  return new Promise((resolve, reject) => {
    uni.request({
      url: `${config.service.host}${config.service.baseURL}${options.url}`,//
      method: options.method, //method
      data: options.method === 'GET' ? options.data : JSON.stringify(options.data), 
      //GET,GETquery String,options.data
      header: {
        'Content-Type': 'application/json; charset=UTF-8',
        'Authorization': 'Bearer ' + store.state.access_token // token                     
      },
      //headertoken
      success(request) {
      //
        if (request.data.code === 200) {
        //200,request.dataresolve
          resolve(request.data)
        } else if (request.data.code === 1000) {
          // 1000access_token
          uni.showModal({
            title: '',
            content: '',
            showCancel: false,
            confirmText: '',
            success: function(res) {
                if (res.confirm) {
                    console.log('')
                }
            }
          })
        } else {
        //,request.datareject
          reject(request.data)
        }
      },
      fail(error) {
      //reject()
        reject(error.data)
      },
      complete() {
        
      }
    })
  })
}

export default request